import React, { useEffect, useState } from "react";
import {
  VictoryChart,
  VictoryLine,
  VictoryScatter,
  VictoryTheme,
  VictoryAxis,
  VictoryTooltip,
  VictoryBrushContainer,
} from "victory";
import dayjs, { Dayjs } from "dayjs";
import advancedFormat from "dayjs/plugin/advancedFormat";
import minMax from "dayjs/plugin/minMax";
import { ContractObject } from "../Contract/Contract";
import ContractDialog from "../ContractDialog/ContractDialog";
import { testContracts } from "../Utils/Utils";
import "./ContractsPage.css";

import DataManager from "../DataManager/DataManager";
import ContractCardList from "../ContractCardList/ContractCardList";

dayjs.extend(minMax);
dayjs.extend(advancedFormat);

const ContractsPage: React.FC = () => {
  const [contracts, setContracts] = useState<ContractObject[]>(testContracts);
  const [earliestStartDate, setEarliestStartDate] = useState<Dayjs | null>(
    null
  );
  const [totalReturn, setTotalReturn] = useState<number>(0);
  const [showChart, setShowChart] = useState<boolean>(false);
  const [brushDomain, setBrushDomain] = useState<any>(null);

  useEffect(() => {
    const startDateArray = contracts.map((contract) => contract.startDate);
    const earliestDate = dayjs.min(startDateArray);
    setEarliestStartDate(earliestDate);

    const totalSellPrice: number = contracts.reduce(
      (totalReturn, contract) => {
        const returnFromContract =
          contract.totalSellPrice - contract.totalBuyBackPrice;
        return totalReturn + returnFromContract;
      },
      0
    );
    setTotalReturn(totalSellPrice);
  }, [contracts]);

  function contractsHandler(contracts: ContractObject): void {
    console.log("ADD MORE CONTRACTS");
    setContracts((prevArray) => [contracts, ...prevArray]);
  }

  const [cumulativeReturnData, setCumulativeReturnData] = useState<{
    x: Date;
    y: number;
    ticker: string;
    total: number;
  }[]>([]);

  useEffect(() => {
    const sortedContracts = contracts
      .slice()
      .sort((a, b) => a.startDate.diff(b.startDate));

    let cumulativeReturn = 0;
    const data = sortedContracts.map((contract) => {
      const returnFromContract =
        contract.totalSellPrice - contract.totalBuyBackPrice;
      cumulativeReturn += returnFromContract;
      return {
        x: contract.startDate.toDate(),
        y: cumulativeReturn,
        ticker: contract.ticker,
        total: contract.totalSellPrice - contract.totalBuyBackPrice,
      };
    });

    setCumulativeReturnData(data);
  }, [contracts]);

  const updateTotalBuyBackPrice = (id: string, newPrice: number) => {
    const updatedContracts = contracts.map((contract) => {
      if (contract.id === id) {
        return { ...contract, totalBuyBackPrice: newPrice };
      }
      return contract;
    });
    setContracts(updatedContracts);
    console.log(updatedContracts);
  };

  const handleChartToggle = () => {
    setShowChart(!showChart);
  };

  const handleBrushDomainChange = (domain: any) => {
    setBrushDomain(domain);
  };

  return (
    <div className="ContractsPage">
      <DataManager
        contracts={contracts}
        restoreContracts={(contracts) => setContracts(contracts)}
        clearContracts={() => setContracts([])}
      />
        <div className="title">Total Return</div>
        <div className="title">
          {"since " + earliestStartDate?.format("MMMM Do")}
        </div>
        <div className="title">{"$" + totalReturn.toLocaleString()}</div>
      <div className="chart-container">
        <div className="chart-toggle">
          <button onClick={handleChartToggle}>
            {showChart ? "Hide Chart" : "Show Chart"}
          </button>
        </div>
        {showChart && (
          <VictoryChart theme={VictoryTheme.material}>
            <VictoryAxis
              dependentAxis
              tickFormat={(tick) => `$${tick.toLocaleString()}`}
            />
            <VictoryAxis
              tickFormat={(tick) => dayjs(tick).format("MMMM Do")}
              style={{
                tickLabels: { fontSize: 10, padding: 5},
              }}
            />
            <VictoryLine data={cumulativeReturnData} />
            <VictoryScatter
              data={cumulativeReturnData}
              labels={({ datum }) =>
                `${datum.ticker}\nReturn: $${datum.total.toLocaleString()}`
              }
              labelComponent={
                <VictoryTooltip
                  cornerRadius={1}
                  flyoutStyle={{ fill: "white" }}
                />
              }
            />
            <VictoryBrushContainer
              brushDimension="x"
              brushDomain={brushDomain}
              onBrushDomainChange={handleBrushDomainChange}
              brushStyle={{
                stroke: "gray",
                fill: "lightgray",
                fillOpacity: 0.3,
              }}
            />
          </VictoryChart>
        )}
      </div>

      <ContractDialog onAddConract={contractsHandler} />
      {contracts && (
        <ContractCardList
          contracts={contracts}
          updateTotalBuyBackPrice={updateTotalBuyBackPrice}
        />
      )}
    </div>
  );
};

export default ContractsPage;
